import axios from "axios";

// GET single post
export async function GET(req, { params }) {
  alert('HAI');
  const { id } = params;
  const authHeader = req.headers.get("authorization");

  try {
    const res = await axios.get(`http://localhost:8000/api/posts/${id}`, {
      headers: { Authorization: authHeader },
    });
    return new Response(JSON.stringify(res.data), { status: res.status });
  } catch (err) {
    return new Response(JSON.stringify({ message: err.response?.data || "Failed" }), {
      status: err.response?.status || 500,
    });
  }
}

// PUT update post
export async function PUT(req, { params }) {
  const { id } = params;
  const body = await req.json();
  const authHeader = req.headers.get("authorization");

  try {
    const res = await axios.put(`http://localhost:8000/api/posts/${id}`, body, {
      headers: { Authorization: authHeader, "Content-Type": "application/json" },
    });
    console.log(res);
    return new Response(JSON.stringify(res.data), { status: res.status });
  } catch (err) {
    return new Response(JSON.stringify({ message: err.response?.data || "Failed" }), {
      status: err.response?.status || 500,
    });
  }
}

// DELETE post (opsional)
export async function DELETE(req, { params }) {
  const { id } = params;
  const authHeader = req.headers.get("authorization");

  try {
    const res = await axios.delete(`http://localhost:8000/api/posts/${id}`, {
      headers: { Authorization: authHeader },
    });
    return new Response(JSON.stringify(res.data), { status: res.status });
  } catch (err) {
    return new Response(JSON.stringify({ message: err.response?.data || "Failed" }), {
      status: err.response?.status || 500,
    });
  }
}
